







































































	export default{
		data() {
			return {
				isIndex:true,
				selItem:0,
				move:2,
				isAlert: false,
				name:'',
				idcard:'',
        playUrl: '',
        getPlayUrling: false
			};
		},
    onLoad: function (option) { 
//       console.log(option.isLogin); 
//     	if(isLogin == true){
//     		this.isAlert = true;
//     	}
    },
    onHide() {
    	this.closeAct()
    },
		methods:{
			goAddteam(){
        this.$navigateTo({
          url: '/pages/index/member'
        })
			},
			goActivation(){
				this.$navigateTo({
					url:'activate'
				})
			},
			// 关闭身份认证
			closeAct(){
				this.isAlert = false;
			},
			gotoInput(){
				this.move = 2;
			},
			InputOver(){
				console.log(this.idcard);
				var reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;  
        if(reg.test(this.idcard) === false) {
					 uni.showToast({
						title: '请输入正确的身份证号码',
					  icon: 'none'
					})
					return
				} 
				else{
					uni.request({
						url: this.$apis.realNameAuth,
						method:'GET',
						header: {
							 Authorization:this.$store.state.jwt,
						},
						data:{
							'real_name':this.name,
							'id_card' :this.idcard
						},
						success: (res) => {
							console.log(res);
						}
					});
				}
			},
      gotoPlay() {
        if (this.getPlayUrling) return
        if (!this.playUrl) {
          this.getPlayUrling = true
          this.$http({
            url: this.$apis.adverts,
            method: 'GET',
            data: {
              place: 11
            },
            cb: (err, data) => {
              if (!err && data.success && data.data[0].link_to_app) {
                this.playUrl = data.data[0].link_to_app
                uni.setStorageSync('webSrc', this.playUrl)
                this.$navigateTo({
                	url: '/pages/web/index'
                })
              }
              this.getPlayUrling = false
            }
          })
        } else {
          uni.setStorageSync('webSrc', this.playUrl)
          this.$navigateTo({
          	url: '/pages/web/index'
          })
        }
      }
		}
	}
